.main-slider {
  z-index: 2;
  width: 100%;
  height: 100%;

  opacity: 0;

  .animate-load & {
    animation-name: fade-in;
    animation-delay: 1s;
    animation-duration: 1.2s;
    animation-timing-function: ease;
    animation-fill-mode: forwards;
  }

  .animate-load-end & {
    opacity: 1;
  }
}

.main-slider__slide-inner {
  position: absolute;
  top: 0;
  right: 27%;
  bottom: 0;
  left: 27%;

  @include vp--1439 {
    right: 20%;
    left: 20%;
  }

  @include vp--767 {
    right: 20%;
    left: 20%;
  }
}

.main-slider--disabled {
  .main-slider__btn {
    opacity: 0.5;
    pointer-events: none;
  }
}

.main-slider__btn {
  top: 49%;
  width: size(60px);
  height: size(60px);

  &::before,
  &::after {
    display: none;
  }

  svg {
    fill: transparent;
    width: 100%;
    height: 100%;
  }

  transition: transform $trans--300, opacity $trans--300, visibility $trans--300;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      transform: scale(1.2667);
    }
  }

  @include vp--1439 {
    top: 50%;
    width: 54px;
    height: 54px;
  }

  @media (min-width: $vp--1024) {
    [data-device="tablet portrait"] & {
      width: 60px;
      height: 60px;
    }
  }

  @include vp--1279 {
    width: 48px;
    height: 48px;
  }

  @include vp--767 {
    top: 52%;
    width: 37px;
    height: 37px;
  }
}

.main-slider__btn--prev {
  left: 21.8%;

  @include vp--1439 {
    left: 12%;
  }

  @include vp--767 {
    left: 5%;
  }
}

.main-slider__btn--next {
  right: 21.8%;

  svg {
    transform: rotate(180deg);
  }

  @include vp--1439 {
    right: 12%;
  }

  @include vp--767 {
    right: 5%;
  }
}
