.description-block {
  position: absolute;
  z-index: 2;

  @include vp--767 {
    position: static;
    padding: 0 16px;
  }

  &__title {
    margin: 0 0 size(33px) 0;

    font-family: $font-family--gotham;
    font-style: italic;
    font-weight: 900;
    font-size: size(60px);
    line-height: 1;
    text-transform: uppercase;

    color: $color-default-white;

    @include vp--767 {
      margin-bottom: 20px;

      font-size: 23px;
    }
  }

  &__sub-title {
    margin: 0;

    font-family: $font-family--helvetica;
    font-weight: 700;
    font-size: size(26px);
    line-height: 1;

    color: $color-default-white;

    @include vp--1023 {
      font-size: size(32px);
      line-height: size(40px);
    }

    @include vp--767 {
      font-size: 18px;
      line-height: 23px;
    }
  }

  &__text {
    font-family: $font-family--gotham;
    font-weight: 400;
    font-size: size(26px);
    line-height: size(34px);

    span {
      font-weight: 700;
    }

    @include vp--1023 {
      font-size: size(32px);
      line-height: size(40px);
    }

    @include vp--767 {
      margin: 10px 0;

      font-size: 18px;
      line-height: 23px;

      br {
        display: none;
      }
    }
  }
}

.description-block--quality {
  max-width: calc(50vw - 105px);

  @include vp--767 {
    max-width: 100%;
    min-width: unset;
  }

  .description-block__sub-title {
    margin-bottom: size(17px);

    font-family: $font-family--helvetica;
    font-size: size(18px);
    line-height: size(23px);
    letter-spacing: 0.3em;
    text-transform: uppercase;
    font-weight: 550;
  }

  .description-block__text {
    margin-bottom: 0;
    margin-top: size(42px);
    line-height: size(35px);
  }
}
